html{
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  background: url('/img/background.jpg');
  background-repeat: no-repeat;
  background-size: 100%;
  min-height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-intro {
    margin-top: 300px;
    text-align: center;
  }
  
.App-header{
    background-color: #222;
    height: 70px;
    padding: 20px;
    color:white;
    text-align: center;
}
.paper{
    margin-top: 50px;
    width: 50%;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
}
.Result{
    background-color: greenyellow;
    text-align: center;
    width: 40%;
    margin-top: 50px;
    margin-right: auto;
    margin-left: auto;
    padding: 20px;
}
